.add-project,
.add-organization {
  width: 760px;

  .platform-label {
    display: inline-block;
    margin-bottom: 10px;
    color: $inputLabelText;
    @include rem(font-size, 14px);
  }

  .checkbox:not(:checked) + label:before,
  .checkbox:checked + label:before {
    margin-top: 5px;
  }

  .checkbox:not(:checked) + label:after,
  .checkbox:checked + label:after {
    top: 8px;
  }

  .main-page-pop-up-buttons {
    margin-top: 25px;
  }

  .popup-platforms {
    display: inline-block;

    label:not(:first-of-type) {
      margin-left: 60px;
    }

    &-block {
      margin-top: -15px;
      margin-bottom: 80px;
    }

    .android,
    .apple,
    .globe {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      color: $white;
      text-align: center;
      line-height: 30px;
      @include rem(font-size, 16px);
    }

    .android {
      background-color: $android-platform;
    }

    .apple {
      background-color: $apple-platform;
    }

    .globe {
      background-color: $globe-platform;
    }
  }

  .btn {
    height: 40px;
  }
}

.add-organization {
  .card-image-load {
    margin-top: 26px;
  }
}

.star {
  &:after {
    content: "*";
    color: $modal-sub-star;
  }
}

