.wrapper {
  position: relative;
  background-color: $grey-1;
  min-height: calc(100vh - 2px);
  padding-bottom: 70px;
}

.section {
  @include em(padding-top, 15px);
  @include em(padding-bottom, 15px);

  &-title {
    font-family: $bold-font-family;
    @include rem(font-size, 32px);
  }
}
