// Buttons style
button {
  &.close {
    padding: 0;
    cursor: pointer;
    background: none;
    border: 0;
  }
}

.btn {
  position: relative;
  display: inline-block;
  margin: 0;
  padding: 5px 20px;
  background-color: transparent;
  border-radius: 4px;

  border: none {
    width: 1px;
    style: solid;
  }

  text: {
    align: center;
    decoration: none;
  }

  white-space: nowrap;
  cursor: pointer;
  outline: none 0;
  vertical-align: middle;
  transition: all 350ms ease-in-out 0s;
  @include rem(font-size, 14px);
  @include no-select();

  &-white-red {
    color: $btn-red;

    &:hover {
      color: darken($btn-red, 10%);
      text-decoration: underline;
    }
  }

  &-red {
    color: $white;
    border-color: $btn-red;
    background-color: $btn-red;

    &:hover {
      border-color: darken($btn-red, 10%);
      background-color: darken($btn-red, 10%);
    }
  }

  &-dark-blue {
    color: $white;
    border-color: $btn-blue;
    border-radius: 0;
    background-color: $btn-blue;

    &:hover {
      border-color: lighten($btn-blue, 20%);
      background-color: lighten($btn-blue, 20%);
    }
  }

  &-white {
    color: $base-text;
    border-color: $btn-white;
    background-color: $btn-white;

    &:hover {
      color: $white;
      border-color: lighten($base-text, 10%);
      background-color: lighten($base-text, 10%);
    }
  }

  &-white-green {
    color: $btn-green;
    border-color: transparent;

    &:hover {
      text-decoration: underline;
      color: darken($btn-green, 10%);
    }
  }

  &-green {
    color: $white;
    border-color: $btn-green;
    background-color: $btn-green;

    &:hover {
      border-color: darken($btn-green, 10%);
      background-color: darken($btn-green, 10%);
    }
  }

  &-ghost {
    color: $btn-green;
    border-color: $btn-green;
    background-color: transparent;

    &:hover {
      color: $white;
      background-color: $btn-green;
    }
  }

  &-ghost-red {
    color: $btn-red;
    border-color: $btn-red;
    background-color: transparent;

    &:hover {
      color: $white;
      background-color: $btn-red;
    }
  }

  &-gray {
    padding: 10px 20px;
    border-radius: 6px;
    color: $white;
    background-color: $btn-gray;
    @include rem(font-size, 16px);

    &:hover,
    &:active,
    &:focus {
      color: $white;
      background-color: darken($btn-gray, 10%);
    }
  }

  &-big {
    padding: 10px 20px;
  }

// Social buttons ------------

  &.fb {
    color: $white;
    border-color: $facebook-color;
    background-color: $facebook-color;

    &:hover {
      border-color: darken($facebook-color, 10%);
      background-color: darken($facebook-color, 10%);
    }
  }

  &.g {
    color: $white;
    border-color: $google-plus-color;
    background-color: $google-plus-color;

    &:hover {
      border-color: darken($google-plus-color, 10%);
      background-color: darken($google-plus-color, 10%);
    }
  }

  &.in {
    color: $white;
    border-color: $linkedin-color;
    background-color: $linkedin-color;

    &:hover {
      border-color: darken($linkedin-color, 10%);
      background-color: darken($linkedin-color, 10%);
    }
  }

  &-nonActive {
    pointer-events: none;
    background-color: rgba(216, 216, 216, .47);
    border-color: rgba(216, 216, 216, .5);
  }

  &.inactive {
    background-color: rgba(216, 216, 216, .47);
    border-color: rgba(216, 216, 216, .5);
  }
}


