.details-org {
  float: left;
  width: 330px;
  padding-top: 15px;

  .input_wrapper {
    margin: 10px 0;
  }

  .your-role-title {
    color: $base-light-text;
    font-family: $light-font-family;
    @include rem(font-size, 14px);
  }

  .role {
    color: $base-text;
    font-family: $bold-font-family;
    @include rem(font-size, 14px);
  }
}

//Organization profile ============================

.org-profile {
  .card-header {
    .org-logo {
      float: left;
      width: 90px;
      height: 90px;
      margin-right: 20px;
      border-radius: 50%;
      border: none;
    }

    &-title {
      margin: 30px 0;
    }

    &-btn {
      margin: 25px 0;
      line-height: 25px;
    }
  }

  &-content {
    width: 100%;
    margin-top: 20px;
    color: $base-text;
    @include rem(font-size, 14px);

    tr {
      height: 50px;

      &:nth-child(2n+1) {
        background-color: $org-content-row-bg;
        border-radius: 6px;
      }

      td {
        border: none;

        &:first-child {
          border-radius: 6px 0 0 6px;
        }

        &:last-child {
          border-radius: 0 6px 6px 0;
        }
      }

      .column-1,
      .column-3 {
        width: 15%;
        font-family: $bold-font-family;
      }

      .column-2 {
        width: 35%;
      }

      .column-3 {
        width: 15%;
      }

      .org-count {
        color: $org-table-count;
      }
    }
  }
}

//Organization members ==============================

.org-members,
.org-projects {

  .card-header-btn {
    line-height: 25px;
  }

  .search-wrap {
    float: right;

    .search {
      margin-right: 25px;
    }

    .search-btn {
      right: 40px;
    }
  }

  &-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 10px;

    td {
      padding-left: 0;
      vertical-align: middle;
      border: none;
    }

    .table-title {
      color: $base-light-text;
      @include rem(font-size, 14px);

      td {
        border-bottom: 1px solid $base-border;
      }
    }

    .org-project,
    .member {
      background-color: $org-content-row-bg;

      td:first-child {
        border-radius: 6px 0 0 6px;
      }

      td:last-child {
        border-radius: 0 6px 6px 0;
      }
    }

    .input_wrapper {
      width: 130px;
    }

.org-role {
  display: block;
  width: 130px;
  height: 22px;
  padding-left: 10px;
  background-color: $selectmenu-small-button;
  color: $selectmenu-small-text;
  line-height: 22px !important;
  vertical-align: text-top;
  border-radius: 2px;
  @include rem(font-size, 12px);
}

    .btn-delete {
      cursor: pointer;

      &:hover {
        .delete {
          fill: darken($red-1, 10%)
        }
      }
    }

    .ic-svg {
      margin: 0 10px 3px 0;
      vertical-align: middle;
    }
  }

}

.org-members-table {
  .member {

    td:last-child {
      padding: 0.4em;
      border-left: 1px solid $base-border;
    }

    &-avatar {
      width: 106px;
    }

    &-avatar img {
      width: 60px;
      height: 60px;
      margin: 12px 0 12px 30px;
      border-radius: 50%;
    }

    &-name {
      width: 210px;
      margin-bottom: 10px;
      color: $base-text;
      font-family: $bold-font-family;
      @extend %text-overflow;
      @include rem(font-size, 14px);
    }

    &-email,
    &-country {
      width: 210px;
      margin: 0;
      color: $base-light-text;
      @extend %text-overflow;
      @include rem(font-size, 14px);
    }

    &-projects,
    &-joined {
      color: $base-text;
      @include rem(font-size, 14px);
    }

    .chevron-down,
    .chevron-up {
      display: block;
      color: $chevron-up-down-color;
      text-align: center;
      cursor: pointer;

      &:before {
        content: "\f078";
        font-family: $font-awesome;
      }
    }

    .chevron-up {
      &:before {
        content: "\f077";
        font-family: $font-awesome;
      }
    }
  }
}

td.org-projects-table-wrap {
  padding: 0 0 0 30px;

  .org-projects-table {
    display: none;

    td {
      padding: 0 0.5em 0 0;
    }

    .table-title {
      td {
        padding: 0.5em 0.5em 0.5em 0;
      }
    }

    .org-project {
      &-avatar {
        width: 106px;
      }

      &-avatar img {
        width: 60px;
        height: 60px;
        margin: 10px 0 10px 30px;
        border-radius: 18px;
      }

      &-name {
        margin-bottom: 10px;
        color: $base-text;
        font-family: $bold-font-family;
        @include rem(font-size, 16px);
      }

      .platform {
        float: left;
        min-width: 80px;
        clear: both;
        @include rem(font-size, 0);

        > div {
          display: inline-block;
          width: 20px;
          height: 20px;
          margin-right: 5px;
          color: $white;
          text-align: center;
          border-radius: 50%;
          @include rem(font-size, 12px);

          > div:nth-child(n+2) {
            margin-left: 5px;
          }

          span {
            line-height: 20px;
          }

          &.android {
            background-color: $android-platform;
          }

          &.apple {
            background-color: $apple-platform;
          }

          &.globe {
            background-color: $globe-platform;
          }
        }
      }

      .distribution-groups {
        color: $base-light-text;
        @include rem(font-size, 14px);
      }
    }
  }
}

// Delete organization ============================

.org-delete {
  .warning-icon {
    float: left;
    margin-right: 20px;
  }

  .warning-description {
    float: left;
    margin-bottom: 90px;
    color: $delete-organization;

    &-title {
      line-height: 14px;
      font-family: $bold-font-family;
      @include rem(font-size, 16px);
    }

    &-text {
      margin: 0;
      @include rem(font-size, 14px);
    }
  }
}

#projects,
#organizationMembers,
#plansAndBillings,
#paymentHistory,
#deleteOrganization {
  display: none;
}

#orgRole1-menu {
  .ui-menu-item {
    .ui-menu-item-wrapper {
      height: 25px;
      padding: 3px 10px;
      line-height: 14px;
    }
  }
}

