// Style for project content

.container-full {
  position: relative;
}

.container-full, .wrapper {
  height: 100%;
}

.content-with-sidebar {
  padding-left: 200px;
}

.main-content {
  padding: 10px 10px 15px 10px;
  margin-top: 69px;
  background-color: $main-content-bg;

  &-title {
    padding: 10px 20px;
    margin-bottom: 10px;
    background-color: $base-bg;
    border-radius: 5px;

    h2 {
      color: #333;
      text-transform: capitalize;
      @include rem(font-size, 24px);
    }
  }
}

.content-with-sidebar {
  .main-content {
    height: calc(100% - #{$header-height});
    overflow: auto;
  }
}
